import React, { useContext } from "react"
import { Box } from "@chakra-ui/react"
import axios from "axios"
import { chakraComponents, AsyncSelect } from "chakra-react-select"

import { graphql, navigate, useStaticQuery } from "gatsby"
import useApiVersioning from "../../../hooks/useApiVersioning"
import { ChevronRightIcon, Search2Icon } from "@chakra-ui/icons"

import FlexSearch from "flexsearch"
import {
  GlobalDispatchContext,
  GlobalStateContext,
} from "../../../context/globalContextProvider"

const customComponents = {
  Control: ({ children, ...props }) => (
    <chakraComponents.Control {...props}>
      <Search2Icon boxSize={6} pl="0.5em" />
      {children}
    </chakraComponents.Control>
  ),
  IndicatorSeparator: () => null,
  DropdownIndicator: props => (
    <chakraComponents.DropdownIndicator {...props}>
      <ChevronRightIcon boxSize={7} />
    </chakraComponents.DropdownIndicator>
  ),
}

function SearchInput() {
  const { selectedApiVersion } = useApiVersioning()
  const dispatch = useContext(GlobalDispatchContext)
  const state = useContext(GlobalStateContext)

  const queryData = useStaticQuery(graphql`
    query {
      allLocalSearchPages {
        nodes {
          publicIndexURL
          publicStoreURL
          version
        }
      }
    }
  `)

  const loadSearchIndex = version => {
    const publicIndexURL = queryData.allLocalSearchPages.nodes.find(
      node => node.version === version
    ).publicIndexURL

    axios.get(publicIndexURL).then(indexResp => {
      let importedIndex = FlexSearch.create()
      importedIndex.import(JSON.stringify(indexResp.data))

      const payload = { [version]: importedIndex }

      dispatch({
        type: "SET_SEARCH_INDEX",
        payload: payload,
      })
    })
  }

  const loadSearchStore = version => {
    const publicStoreURL = queryData.allLocalSearchPages.nodes.find(
      node => node.version === version
    ).publicStoreURL

    axios.get(publicStoreURL).then(storeResp => {
      const payload = { [version]: storeResp.data }

      dispatch({
        type: "SET_SEARCH_STORE",
        payload: payload,
      })
    })
  }

  const handleFocus = () => {
    if (state.searchIndex[selectedApiVersion.name] == null) {
      loadSearchIndex(selectedApiVersion.name)
    }

    if (state.searchStore[selectedApiVersion.name] == null) {
      loadSearchStore(selectedApiVersion.name)
    }
  }

  const search = (query, index, store) => {
    if (!query || !index || !store) return []
    const rawResults = index.search(query)
    return rawResults.map(function (id) {
      return store[id]
    })
  }

  const navigateToSearchResult = async path => {
    try {
      await navigate(`/${path}`)
    } catch (e) {
      console.error(e)
    }
  }

  const filterSearchResults = query => {
    const searchResults = search(
      query,
      state.searchIndex[selectedApiVersion.name],
      state.searchStore[selectedApiVersion.name]
    )
    return searchResults.filter(
      result =>
        result.title !== null &&
        result.title !== "" &&
        result.path !== null &&
        !result.path.toLowerCase().endsWith("/toc") &&
        result.path.toLowerCase() !== "toc"
    )
  }

  const filteredSearchResults = query => {
    const results = filterSearchResults(query)

    return new Promise(resolve => {
      resolve(results)
    })
  }

  const formatOptionLabel = ({ title, description }) => (
    <Box cursor="pointer" px={1} rounded="sm">
      <Box fontWeight="semibold" lineHeight="tight" noOfLines={1}>
        {title}
      </Box>
      {description && (
        <Box color="gray.600" fontSize="sm" noOfLines={1}>
          {description}
        </Box>
      )}
    </Box>
  )

  return (
    <Box>
      <AsyncSelect
        controlShouldRenderValue={false}
        placeholder="Search our documentation"
        aria-label="Search our documentation"
        noOptionsMessage={() => "No results found"}
        onChange={selectedOption => {
          navigateToSearchResult(selectedOption.path)
        }}
        formatOptionLabel={formatOptionLabel}
        components={customComponents}
        loadOptions={filteredSearchResults}
        onFocus={handleFocus}
        chakraStyles={{
          control: styles => ({
            ...styles,
            background: "white",
            textAlign: "left",
          }),
          dropdownIndicator: styles => ({ ...styles, background: "white" }),
          noOptionsMessage: styles => ({
            ...styles,
            textAlign: "left",
            paddingLeft: "1em",
          }),
          option: (styles, state) => ({
            ...styles,
            backgroundColor: state.isSelected
              ? "rlYellow"
              : state.isFocused
              ? "rlYellow40"
              : null,
          }),
          menuList: styles => ({ ...styles, padding: "0.75em" }),
        }}
      />
    </Box>
  )
}

export default SearchInput
